                    <template v-slot:item.updated_on="{ item }" >
                      {{item.updated_on | timezone}}
                    </template>
                    <template v-slot:item.ts_value="{ item }" >
                      {{item.ts_value | timezone}}
                    </template>
                    <template v-slot:item.ts_fault="{ item }" >
                      {{item.ts_fault | timezone}}
                    </template>
