                          <v-text-field v-model="item.register" single-line type='number' min="0" @input="numberChange()" ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.register_lesngth="{ item }">
                      <v-edit-dialog :return-value.sync="item.register_length" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.register_length }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.register_length" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.function_code="{ item }">
