                        <template v-slot:input>
                          <v-select v-model="item.data_endian" :items="point_data_endians"></v-select>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value_round="{ item }">
                      <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
