                        <template v-slot:input>
                          <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value_offset="{ item }">
                      <v-edit-dialog :return-value.sync="item.value_offset" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.value_offset }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.value_offset" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value_raw="{ item }">
                      <v-edit-dialog :return-value.sync="item.value_raw" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
