                        <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.writable="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('writable') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('writable')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.register_length="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('register_length') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('register_length')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.data_type="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('data_type') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('data_type')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.data_endian="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('data_endian') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('data_endian')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.value_raw="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('value_raw') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('value_raw')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.value_round="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('value_round') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('value_round')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.value_offset="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('value_offset') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('value_offset')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.value_operation="{ item }" >
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('value_operation') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('value_operation')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.cov_threshold="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('cov_threshold') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('cov_threshold')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.history_enable="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('history_enable') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('history_enable')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.history_type="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('history_type') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('history_type')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.history_interval="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('history_interval') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('history_interval')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>
                    <template v-slot:header.value="{ item }">
                      <div class="d-flex">
                        <span>{{ toUpperTextKey('value') }}</span>&nbsp;&nbsp;
                        <v-icon x-small @click="openMassEditDialog('value')" color="primary">mdi-pencil</v-icon>
                      </div>
                    </template>

                    <!-- expand-edit-row -->
                    <template v-slot:item.data-table-expand="{ expand, isExpanded }">
                      <v-icon @click="expand(!isExpanded)">{{ isExpanded ? 'mdi-menu-up' : 'mdi-menu-down' }}</v-icon>
                    </template>
                    <template v-slot:expanded-item="{ headers, item }">
                      <td colspan="2"></td>
                      <td class="actions">
                        <v-icon small @click="updatePoint(editedItem)" color="primary"> mdi-content-save </v-icon>
                      </td>
                      <td class="name">
                        <v-text-field v-model="editedItem.name" required :rules="[() => !isExistingvalue(editedItem.name, 'name', editedItem) || 'Name Existed', rules.required]" ></v-text-field>
                      </td>
                      <td class="driver"></td>
                      <td class="enable">
                        <v-checkbox v-model="editedItem.enable" ></v-checkbox>
                      </td>
                      <td class="function_code">
                        <v-select v-model="editedItem.function_code" :items="point_types" required :rules="[rules.required]"></v-select>
                      </td>
                      <td class="register">
                        <v-text-field v-model="editedItem.register" :rules="[() => !isExistingvalue(editedItem.register, 'register', editedItem) || 'Register Existed']" type="number" ></v-text-field>
                      </td>
                      <td class="register_length">
                        <v-text-field v-model="editedItem.register_length" type="number" ></v-text-field>
                      </td>
                      <td class="cov_threshold">
                        <v-text-field v-model="editedItem.cov_threshold" type="number"></v-text-field>
                      </td>
                      <td class="data_type">
                        <v-select v-model="editedItem.data_type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                      </td>
                      <td class="data_endian">
                        <v-select v-model="editedItem.data_endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                      </td>
                      <td class="value">
                        <v-text-field v-model="editedItem.value" type="number"></v-text-field>
                      </td>
                      <td class="value_original"></td>
                      <td class="value_round">
                        <v-text-field v-model="editedItem.value_round" type="number"></v-text-field>
                      </td>
                      <td class="value_offset">
                        <v-text-field v-model="editedItem.value_offset" type="number"></v-text-field>
                      </td>
                      <td class="value_operation">
                        <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                      </td>
                      <td class="value_raw"></td>
                      <td class="input_max">
                        <v-text-field v-model="editedItem.input_max" type="number"></v-text-field>
                      </td>
                      <td class="input_min">
                        <v-text-field v-model="editedItem.input_min" type="number"></v-text-field>
                      </td>
                      <td class="scale_max">
                        <v-text-field v-model="editedItem.scale_max" type="number"></v-text-field>
                      </td>
                      <td class="scale_min">
                        <v-text-field v-model="editedItem.scale_min" type="number"></v-text-field>
                      </td>
                      <td class="writable">
                        <v-checkbox v-model="editedItem.writable"></v-checkbox>
                      </td>
