                  </v-data-table>
                  <div v-else>No data</div>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-tab-item>
          <v-tab-item :key="2">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
        </v-tabs-items>
      </div>
