    <!--Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Point</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="Point name" required :rules="[() => !isExistingvalue(editedItem.name, 'name', editedItem) || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.cov_threshold" label="cov threshold" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.timeout_global" label="timeout global"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.function_code" label="function_code" :items="point_types" required :rules="[rules.required]" v-on:change="$refs.form.resetValidation()" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.data_type" label="data type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.data_endian" label="data endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.register" label="register" :rules="[() => !isExistingvalue(editedItem.register, 'register', editedItem) || 'Register Existed']" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.register_length" label="register length" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="timeout" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.writable" label="writable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_offset" label="value offset" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_raw" label="value raw"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="History" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.history_enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.history_type" label="Type" :items="history_data_types" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.history_interval" label="Interval" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="updatePoint(editedItem)" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-select v-if="editField === 'data_type'" v-model="editAll[editField]" :items="point_data_types" :label="toUpperTextKey(editField)" ></v-select>
            <v-select v-if="editField === 'data_endian'" v-model="editAll[editField]" :items="point_data_endians" :label="toUpperTextKey(editField)" ></v-select>
            <v-text-field v-if="editField === 'value_operation'" v-model="editAll[editField]" :items="value_operation" :label="toUpperTextKey(editField)" ></v-text-field>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'function_code' && editField !== 'data_type' && editField !== 'data_endian' && editField !== 'value_operation'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { toUpperTextKey } from '../../utils/utils';
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import MbRightSidebar from './MbRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';

export default {
  components: {
    MqttConsole,
    MbRightSidebar,
    ConfirmDialog
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    action: '',
    network: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    pointHeaders: [
      { text: 'Name', value: 'name', width: '90px' , groupable: false },
      { text: 'Enable', value: 'enable', sortable: false , groupable: false },
      { text: 'Value', value: 'value', sortable: false , groupable: false },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false , groupable: false },
      { text: 'Writable', value: 'writable', sortable: false , groupable: false },
      { text: 'Fault', value: 'fault', sortable: false , groupable: false },
      { text: 'Write value', value: 'priority_array_write', sortable: false , groupable: false },
    ],
    advancedHeaders: [
      { text: 'Actions', value: 'actions', sortable: false , groupable: false },
      { text: 'Name', value: 'name', width: '90px' , groupable: false },
      { text: 'Driver', value: 'driver', sortable: false , groupable: false },
      { text: 'Enable', value: 'enable', sortable: false , groupable: false },
      { text: 'Function code', value: 'function_code', sortable: false , groupable: false },
      { text: 'Register', value: 'register', sortable: false , groupable: false },
      { text: 'Register length', value: 'register_length', sortable: false , groupable: false },
      { text: 'COV threshold', value: 'cov_threshold', sortable: false , groupable: false },
      { text: 'Data type', value: 'data_type', sortable: false , groupable: false },
      { text: 'Data endian', value: 'data_endian', sortable: false , groupable: false },
      { text: 'Value', value: 'value', sortable: false , groupable: false },
      { text: 'Value original', value: 'value_original', sortable: false , groupable: false },
      { text: 'Value round', value: 'value_round', sortable: false , groupable: false },
      { text: 'Value offset', value: 'value_offset', sortable: false , groupable: false },
      { text: 'Value operation', value: 'value_operation', sortable: false , groupable: false },
      { text: 'Value raw', value: 'value_raw', sortable: false , groupable: false },
      { text: 'Input max', value: 'input_max', sortable: false , groupable: false },
      { text: 'Input min', value: 'input_min', sortable: false , groupable: false },
      { text: 'Scale max', value: 'scale_max', sortable: false , groupable: false },
      { text: 'Scale min', value: 'scale_min', sortable: false , groupable: false },
      { text: 'Writable', value: 'writable', sortable: false , groupable: false },
      { text: 'Fault', value: 'fault', sortable: false , groupable: false },
      { text: 'Write value', value: 'priority_array_write', sortable: false , groupable: false },
      { text: 'Fault message', value: 'fault_message', sortable: false , groupable: false },
      { text: 'Fault timestamp', value: 'ts_fault', sortable: false , groupable: false },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false , groupable: false },
      { text: 'Created on', value: 'created_on', sortable: false , groupable: false },
      { text: 'Updated on', value: 'updated_on', sortable: false , groupable: false },
      { text: 'Device uuid', value: 'device_uuid', sortable: false , groupable: false },
      { text: 'UUID', value: 'uuid', align: 'start', sortable: false , groupable: false },
    ],
    point_types: [
      'READ_COILS',
      'READ_DISCRETE_INPUTS',
      'READ_HOLDING_REGISTERS',
      'READ_INPUT_REGISTERS',
      'WRITE_COIL',
      'WRITE_REGISTER',
      'WRITE_COILS',
      'WRITE_REGISTERS',
    ],
    point_data_types: ['RAW', 'INT16', 'UINT16', 'INT32', 'UINT32', 'FLOAT', 'DOUBLE', 'DIGITAL'],
    point_data_endians: ['LEB_BEW', 'LEB_LEW', 'BEB_LEW', 'BEB_BEW'],
    value_operation: "x + 0",
    history_data_types: ['INTERVAL', 'COV'],
    currentItem: {},
    editedItem: { },
    rules: {
      required: value => !!value || 'Required.',
    },
    search: '',
    dialogMassEdit: false,
    editAll: {
      enable: false,
      register_length: 0,
      function_code: "READ_HOLDING_REGISTERS",
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'x + 0"',
      writable: false,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
      input_min: 0,
      input_max: 0,
      scale_min: 0,
      scale_max: 0,
      value: 0,
    },
    editField: "",
    intervalId: null,
    expanded: [],
    pointsOfDevice: [],
    dataTable: {},
    dirs: [{text: "NETWORKS", link: '/modbus/networks'}, { text: 'POINTS VIEW', disabled: true }],
    advancedView: false,
    networks: [],
  }),
  computed: {
    refreshTime() {
      const { setting } = this.$store.state;
      return setting.refreshTime;
    },
    enableAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.enableAutoRefresh;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getAllPoints();
      }
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllPoints();
        if(this.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.refreshTime}ms`, { type: 'info' });
        }
      }, this.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      axios.get(`point-server/network?with_children=true&points=true`).then(data => {
        this.networks = data;
        this.getDataTable(data);
      });
    },
    getDataTable(networks){
      this.dataTable = {};
      networks.forEach(network=>{
        this.dataTable[network.name] = [];
        network.devices.forEach(device=>{
          if(device.points.length > 0){
            const pointsPerDevice = device.points.map(point=>{
              const keys = Object.keys(point.point_store);
              if (keys && keys.length > 0) {
                keys.forEach(key => {
                  point = { ...point, [key]: point.point_store[key], category: `DEVICE: ${device.name}`, network_name: network.name };
                });
              }
              delete point.point_store;
              delete point.point_uuid;
              return point;
            });
            this.dataTable[network.name] = this.dataTable[network.name].concat(pointsPerDevice);
          }
        });
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable[item.network_name].findIndex(p=>p.uuid == item.uuid);
      this.pointsOfDevice = this.dataTable[item.network_name].filter(point=>point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;

      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable[this.currentItem.network_name].indexOf(this.currentItem);
      this.dataTable[this.currentItem.network_name].splice(this.editedIndex, 1);
      this.dataTable[this.currentItem.network_name] = [...this.dataTable[this.currentItem.network_name]];
      await axios.delete(`point-server/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(value, fieldName, item) {
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p=>p.uuid == item.uuid);
      points.splice(index, 1);
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      axios.put(`point-server/point/${data.uuid}`, data)
        .then(response => {
          const index = this.dataTable[data.network_name].findIndex(n => n.uuid == response.uuid);
          Object.assign(this.dataTable[data.network_name][index], response);
          this.$toasted.show('update success');
          this.close();
          this.expanded = [];
        });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor + "set-row-height";
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        enable: false,
        register_length: 0,
        function_code: "READ_HOLDING_REGISTERS",
        data_type: 'RAW',
        data_endian: 'BEB_LEW',
        value_round: 1,
        value_offset: 0,
        value_operation: 'x + 0"',
        writable: false,
        cov_threshold: 0,
        history_enable: false,
        history_type: 'INTERVAL',
        history_interval: 15,
        input_min: 0,
        input_max: 0,
        scale_min: 0,
        scale_max: 0,
        value: 0,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({item}) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh(){
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    }
  },
};
</script>
